"use client";

import { useState } from 'react';
import { Card, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Verse } from '@/lib/types';
import { storage } from '@/lib/storage';
import { cn } from '@/lib/utils';

interface VerseCardProps {
  verse: Verse;
  showReflection?: boolean;
  showFavoriteButton?: boolean;
  isFavorite?: boolean;
  onFavoriteChange?: (isFavorite: boolean) => void;
  className?: string;
}

export function VerseCard({ 
  verse, 
  showReflection = true, 
  showFavoriteButton = true,
  isFavorite: initialIsFavorite,
  onFavoriteChange,
  className 
}: VerseCardProps) {
  const [isFavorite, setIsFavorite] = useState(() => {
    if (initialIsFavorite !== undefined) return initialIsFavorite;
    const favorites = storage.getFavorites();
    return favorites.some(fav => fav.reference === verse.reference && fav.text === verse.text);
  });

  const handleFavoriteToggle = () => {
    if (isFavorite) {
      const favorites = storage.getFavorites();
      const favoriteToRemove = favorites.find(fav => 
        fav.reference === verse.reference && fav.text === verse.text
      );
      if (favoriteToRemove) {
        storage.removeFavorite(favoriteToRemove.id);
      }
      setIsFavorite(false);
      onFavoriteChange?.(false);
    } else {
      storage.addFavorite(verse);
      setIsFavorite(true);
      onFavoriteChange?.(true);
    }
  };

  const handleShare = async () => {
    const shareText = `"${verse.text}" - ${verse.reference}`;
    
    if (navigator.share) {
      try {
        await navigator.share({
          title: 'Daily Bible Verse',
          text: shareText,
        });
      } catch (error) {
        console.log('Share cancelled');
      }
    } else {
      try {
        await navigator.clipboard.writeText(shareText);
        alert('Verse copied to clipboard!');
      } catch (error) {
        console.error('Failed to copy to clipboard');
      }
    }
  };

  return (
    <Card className={cn(
      "bg-white/70 dark:bg-slate-800/70 backdrop-blur-sm border-amber-200 dark:border-slate-600 shadow-lg",
      className
    )}>
      <CardContent className="p-6">
        <blockquote className="text-lg md:text-xl leading-relaxed text-slate-800 dark:text-slate-200 font-serif italic mb-4">
          "{verse.text}"
        </blockquote>
        
        <div className="flex items-center justify-between mb-4">
          <cite className="text-amber-700 dark:text-amber-400 font-semibold not-italic">
            — {verse.reference}
          </cite>
          <span className="text-sm text-slate-500 dark:text-slate-400 bg-slate-100 dark:bg-slate-700 px-2 py-1 rounded">
            {verse.translation}
          </span>
        </div>

        {showReflection && verse.reflection && (
          <div className="bg-amber-50 dark:bg-amber-900/20 border-l-4 border-amber-400 p-4 mb-4 rounded-r">
            <p className="text-slate-700 dark:text-slate-300 text-sm leading-relaxed">
              {verse.reflection}
            </p>
          </div>
        )}

        <div className="flex items-center justify-between pt-2">
          <div className="flex gap-2">
            {showFavoriteButton && (
              <Button
                variant="ghost"
                size="sm"
                onClick={handleFavoriteToggle}
                className={cn(
                  "transition-colors duration-200",
                  isFavorite 
                    ? "text-red-500 hover:text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20" 
                    : "text-slate-500 hover:text-red-500 hover:bg-red-50 dark:hover:bg-red-900/20"
                )}
              >
                <svg 
                  className="w-5 h-5 mr-1" 
                  fill={isFavorite ? "currentColor" : "none"} 
                  stroke="currentColor" 
                  viewBox="0 0 24 24"
                >
                  <path 
                    strokeLinecap="round" 
                    strokeLinejoin="round" 
                    strokeWidth={2} 
                    d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" 
                  />
                </svg>
                {isFavorite ? 'Saved' : 'Save'}
              </Button>
            )}
            
            <Button
              variant="ghost"
              size="sm"
              onClick={handleShare}
              className="text-slate-500 hover:text-blue-500 hover:bg-blue-50 dark:hover:bg-blue-900/20"
            >
              <svg className="w-5 h-5 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.367 2.684 3 3 0 00-5.367-2.684z" />
              </svg>
              Share
            </Button>
          </div>

          {verse.topic && (
            <span className="text-xs bg-amber-100 dark:bg-amber-900/30 text-amber-700 dark:text-amber-400 px-2 py-1 rounded-full">
              {verse.topic}
            </span>
          )}
        </div>
      </CardContent>
    </Card>
  );
}
