"use client";

import { useEffect, useState } from 'react';
import { VerseCard } from '@/components/verse-card';
import { MobileNavigation } from '@/components/mobile-navigation';
import { verseService } from '@/lib/verse-service';
import { Verse } from '@/lib/types';

export default function HomePage() {
  const [dailyVerse, setDailyVerse] = useState<Verse | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const loadDailyVerse = () => {
      try {
        const verse = verseService.getDailyVerse();
        setDailyVerse(verse);
      } catch (error) {
        console.error('Failed to load daily verse:', error);
      } finally {
        setIsLoading(false);
      }
    };

    loadDailyVerse();
  }, []);

  const getCurrentDate = () => {
    const today = new Date();
    return today.toLocaleDateString('en-US', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center pb-20">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-amber-600 mx-auto mb-4"></div>
          <p className="text-slate-600 dark:text-slate-400">Loading today's verse...</p>
        </div>
        <MobileNavigation />
      </div>
    );
  }

  return (
    <div className="min-h-screen pb-20">
      {/* Header */}
      <header className="bg-white/80 dark:bg-slate-900/80 backdrop-blur-lg border-b border-amber-200 dark:border-slate-700 sticky top-0 z-40">
        <div className="max-w-md mx-auto px-4 py-4">
          <div className="text-center">
            <h1 className="text-2xl font-bold text-amber-800 dark:text-amber-200 mb-1">
              DailyBread
            </h1>
            <p className="text-sm text-slate-600 dark:text-slate-400">
              {getCurrentDate()}
            </p>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-md mx-auto px-4 py-6">
        {/* Welcome Message */}
        <div className="text-center mb-8">
          <h2 className="text-xl font-semibold text-slate-800 dark:text-slate-200 mb-2">
            Today's Verse
          </h2>
          <p className="text-slate-600 dark:text-slate-400 text-sm">
            Your daily spiritual nourishment
          </p>
        </div>

        {/* Daily Verse */}
        {dailyVerse && (
          <div className="mb-8">
            <VerseCard 
              verse={dailyVerse}
              showReflection={true}
              showFavoriteButton={true}
            />
          </div>
        )}

        {/* Quick Actions */}
        <div className="grid grid-cols-2 gap-4 mb-8">
          <a
            href="/explore"
            className="bg-white/70 dark:bg-slate-800/70 backdrop-blur-sm border border-amber-200 dark:border-slate-600 rounded-lg p-4 text-center hover:bg-amber-50 dark:hover:bg-slate-700 transition-colors duration-200"
          >
            <div className="text-amber-600 dark:text-amber-400 mb-2">
              <svg className="w-8 h-8 mx-auto" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
              </svg>
            </div>
            <h3 className="font-medium text-slate-800 dark:text-slate-200 text-sm">
              Explore
            </h3>
            <p className="text-xs text-slate-600 dark:text-slate-400 mt-1">
              Random verses
            </p>
          </a>

          <a
            href="/favorites"
            className="bg-white/70 dark:bg-slate-800/70 backdrop-blur-sm border border-amber-200 dark:border-slate-600 rounded-lg p-4 text-center hover:bg-amber-50 dark:hover:bg-slate-700 transition-colors duration-200"
          >
            <div className="text-red-500 mb-2">
              <svg className="w-8 h-8 mx-auto" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
              </svg>
            </div>
            <h3 className="font-medium text-slate-800 dark:text-slate-200 text-sm">
              Favorites
            </h3>
            <p className="text-xs text-slate-600 dark:text-slate-400 mt-1">
              Saved verses
            </p>
          </a>
        </div>

        {/* Inspirational Quote */}
        <div className="bg-gradient-to-r from-amber-100 to-blue-100 dark:from-amber-900/20 dark:to-blue-900/20 rounded-lg p-4 text-center">
          <p className="text-sm text-slate-700 dark:text-slate-300 italic">
            "Your word is a lamp for my feet, a light on my path."
          </p>
          <p className="text-xs text-amber-700 dark:text-amber-400 mt-1">
            — Psalm 119:105
          </p>
        </div>
      </main>

      <MobileNavigation />
    </div>
  );
}
