import { Verse } from '@/lib/types';
import { storage } from '@/lib/storage';
import versesData from '@/lib/data/verses.json';

export class VerseService {
  private verses: Verse[] = versesData;

  // Get today's verse (deterministic based on date)
  getDailyVerse(): Verse {
    const today = new Date().toDateString();
    const lastVerseDate = storage.getDailyVerseDate();
    
    // If we already have today's verse, return it
    if (lastVerseDate === today) {
      const cachedVerse = storage.getDailyVerse();
      if (cachedVerse) return cachedVerse;
    }

    // Generate a deterministic index based on today's date
    const dateHash = this.hashDate(today);
    const verseIndex = dateHash % this.verses.length;
    const dailyVerse = this.verses[verseIndex];

    // Cache the verse for today
    storage.setDailyVerseDate(today);
    storage.setDailyVerse(dailyVerse);

    return dailyVerse;
  }

  // Get a random verse
  getRandomVerse(): Verse {
    const randomIndex = Math.floor(Math.random() * this.verses.length);
    return this.verses[randomIndex];
  }

  // Search verses by text, reference, or topic
  searchVerses(query: string): Verse[] {
    const searchTerm = query.toLowerCase().trim();
    if (!searchTerm) return [];

    return this.verses.filter(verse => 
      verse.text.toLowerCase().includes(searchTerm) ||
      verse.reference.toLowerCase().includes(searchTerm) ||
      (verse.topic && verse.topic.toLowerCase().includes(searchTerm)) ||
      (verse.reflection && verse.reflection.toLowerCase().includes(searchTerm))
    );
  }

  // Get verses by topic
  getVersesByTopic(topic: string): Verse[] {
    return this.verses.filter(verse => 
      verse.topic && verse.topic.toLowerCase() === topic.toLowerCase()
    );
  }

  // Get all unique topics
  getTopics(): string[] {
    const topics = this.verses
      .map(verse => verse.topic)
      .filter((topic): topic is string => Boolean(topic));
    return [...new Set(topics)].sort();
  }

  // Get all verses
  getAllVerses(): Verse[] {
    return this.verses;
  }

  // Simple hash function for date consistency
  private hashDate(dateString: string): number {
    let hash = 0;
    for (let i = 0; i < dateString.length; i++) {
      const char = dateString.charCodeAt(i);
      hash = ((hash << 5) - hash) + char;
      hash = hash & hash; // Convert to 32-bit integer
    }
    return Math.abs(hash);
  }
}

export const verseService = new VerseService();
